import { Looker40SDK } from "@looker/sdk";
import { PromptRepository } from "../databases/PromptRepository";
import { LookerSQLService } from "./LookerSQLService";
import { ConfigReader } from "./ConfigReader";
import { Logger } from "../utils/Logger"

export class RagService {

    private sql: LookerSQLService;

    public constructor(lookerSDK: Looker40SDK) {
        this.sql = new LookerSQLService(lookerSDK);        
    }


    /**
     * Update the settings for the current user
     * @param updatedSettings 
     * @returns 
     */
    public async insertExploreModelsRAG(values_string: string)
    {
        try{
            const queryToRun = `#Looker GenAI Extension - version: ${ConfigReader.CURRENT_VERSION} - insertExploreRAG            
            INSERT INTO ${ConfigReader.EXPLORE_MODELS} (explore_name, label, name, description, type)
            VALUES ${values_string}`;        
            const results = await this.sql.executeLog(queryToRun);
            Logger.info("Inserted Explore Model Fields Sucessfully: "+ results);             
        }
        catch(err)
        {
            Logger.error("Failed to Insert Explore Model Fields for RAG");
        }
        
    }  

}